@import url('https://fonts.googleapis.com/css2?family=Bungee&display=swap');

body {
  font-family: "Bungee", Helvetica, sans-serif;
  background-color: rgb(18, 18, 19);
}
.game-row {
  display: flex;
  justify-content: center;
}
p, .react-icon {
  color: #ffffff;
  margin: 0;
  padding: 0;
  text-align: center;
}

.letter-box {
  width: 55px;
  height: 58px;
  font-size: 1.8rem;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 3px;
  border-radius: 10% 10% 10% 10%;
}
.empty-letter {
  background-color: rgb(58, 58, 60);
}
.guessed-letter {
  background-color: rgb(83, 141, 78);
}
.half-letter {
  background-color: rgb(181, 159, 59);
}

.letter-box {
  transition: transform 0.5s linear, background-color 0.5s ease-in-out, width 0.5s linear;
  transform-style: preserve-3d;
}

.letter-flip {
  transform: rotateX(360deg);
}

.letter {
  backface-visibility: hidden;
}

.game-state {
  font-family: cursive;
  font-size: 1.5rem;
  margin-top: 20px;
  font-weight: bold;
  font-style:italic;
}

.key-row {
  display: grid;
  justify-content: center;
  gap: 6px; /* spacing between keys */
  width: 100%;
  max-width: 500px;
  margin-bottom: 5px;
}

.key-row.row-1 {
  grid-template-columns: repeat(10, 1fr); /* 10 keys in row 1 */
}

.key-row.row-2 {
  grid-template-columns: repeat(9, 1fr); /* 9 keys in row 2 */
}

.key-row.row-3 {
  grid-template-columns: 1.5fr repeat(7, 1fr) 1.5fr; /* enter + 7 keys + backspace */
}

.key-box {
  background-color: rgb(92, 92, 92);
  width: 100%;
  height: 58px;
  font-size: 1.5rem;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  padding: 0;
  border-radius: 10% 10% 10% 10%;
  user-select: none;
}
.key-box:active {
  background-color: rgb(169, 169, 169);
}
.key-box > p {
  padding: 5px;
  user-select: none;
}
.longer {
  width: 40px;
}
.keyboard-grid {
  background-color: rgb(18, 18, 19);
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  max-width: 500px;
  z-index: 999;
  margin-top: 10px;
  margin-bottom: 20px;
}

.game-grid {
  padding-bottom: 250px;
}